import { IgcPivotDateDimension } from 'igniteui-angular-elements/elements.js';
import { IgcGridBaseDirective } from 'igniteui-webgrids/igc-grid-base-directive';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcGridExtras {
    private static _registered = false;
    public static ensureRegistered() {
        if (!IgcGridExtras._registered) {
            IgcGridExtras._registered = true;
            (IgcGridBaseDirective.prototype as any).beforeColumnsAutogenerated = (inst, args) => {
                let columns = args.detail.columns;
                for (var i = 0; i < columns.count; i++) {
                    var c = columns.item(i);
                    c._implementation.___mustPassByValue = true;
                    c._implementation.___mustPassByValueOnce = true;
                }
            };
            TypeRegistrar.registerCons("WebPivotDateDimension", IgcPivotDateDimension);
        }
    }
}